playButtonsFirst.forEach((el) => {
  el.addEventListener('click', (e) => {
    let video = e. currentTarget.closest('.main-slider__media').querySelector('video');
    video.play();
    e. currentTarget.style.display = 'none';
    setTimeout(() => {
      video.volume = 1;
    }, 1000 );

  })
});


let price = document.querySelector('.price');
let buy = document.querySelector('.buy');
let ok = document.querySelector('.ok');
let promokod = document.querySelector('.promokod__input');
let red = document.querySelector('.red');
// let sangrita = 'SANGRITA';


ok.onclick =function(event) {
  event.preventDefault();
  let up = promokod.value.toUpperCase();
  up = up.trim();
  if (up == 'SANGRITA4') {
    red.innerHTML = `Промокод применён, <br> цена билета 250 руб.`
    price.value = '250';
  } else if (up == 'TATARTRIP'){
    red.innerHTML = `Промокод применён, <br> цена билета 170 руб.`
    price.value = '170';
  } else  {
      red.innerHTML = 'Промокод не найден'
    }
};


